// @flow

import React, { Component } from 'react';
import { connect } from 'react-redux';

import AuthInput from '../../components/inputs/AuthInput';
import CommonButton from '../../components/buttons/CommonButton';

type Props = {
  activeTab: {
    name: '',
  },
  tabData: [],
};

class Content extends Component {
  props: props;

  buttonClickHandler() {
    console.log('auth button click');
    console.log(this.loginUsername);
  }

  render() {
    const { activeTab: { name }, tabData } = this.props;
    const loginTabMark = (name === tabData[0].name) ?
      (
        <section className="panel">
          <div className="item-container">
            <AuthInput ref={el => { this.loginUsername = el; }} placeholder="User Name" type="text" />
          </div>
          <div className="item-container">
            <AuthInput placeholder="Password" type="password" />
          </div>
          <div className="item-container">
            <CommonButton title="Login" className="auth-button" onClick={() => this.buttonClickHandler()} />
          </div>
        </section>
      ) :
      null;
    const registerTabMark = (name === tabData[1].name) ?
      (
        <section className="panel">
          <div className="item-container">
            <AuthInput placeholder="User Name" type="text" />
            <AuthInput placeholder="User Name" type="text" />
          </div>
          <div className="item-container">
            <AuthInput placeholder="Password" type="password" />
          </div>
          <div className="item-container">
            <CommonButton title="Login" className="auth-button" onClick={() => this.buttonClickHandler()} />
          </div>
        </section>
      ) :
      null;

    return (
      <div>
        {loginTabMark}
        {registerTabMark}
      </div>
    );
  }
}

export default Content;



